<template>
  <div>
    <a-page-header
        style="border: 1px solid rgb(235, 237, 240)"
        :title="title"
        :sub-title="subTitle"
        @back="() => $router.go(-1)"
    >
      <template slot="extra">
        <a-button key="3" @click="handleRandomItem">
          Random Refresh
        </a-button>
        <a-button key="2" @click="handleClearCache">
          Clear Cache
        </a-button>
        <a-button key="1" type="primary" @click="showDrawer">
          Setting
        </a-button>
      </template>
    </a-page-header>

    <a-drawer
        title="Card Setting"
        placement="right"
        :closable="false"
        :visible="visible"
        width="30%"
        size="small"
        @close="onClose"
    >
      <item-list :data-source="dataSource"></item-list>
    </a-drawer>

    <a-empty v-show="showEmpty"/>
    <a-alert
        message="Notes Info"
        description="Pleace click 'Setting' button, add option."
        type="info"
        show-icon
        closable
        @close="isShowAlert = false"
        v-show="isShowAlert"
    />
    <div class="content">
      <h2 style="color: #A7AFBA">{{ randomMember.title || 'please click \'Go\' button' }}</h2>
    </div>
    <div class="action">
      <a-button type="primary" shape="round" size="large" :loading="isRunning" @click="go">Go</a-button>
    </div>
  </div>
</template>

<script>
import {Button, PageHeader, Drawer, Input, Icon, Tooltip, Empty, Alert} from 'ant-design-vue'

import ItemList from './list/itemList'
import {createNamespacedHelpers} from 'vuex'
const {mapMutations} = createNamespacedHelpers('luckCardModule')

export default {
  name: "LuckSimple",
  components: {
    ItemList,
    [Empty.name]: Empty,
    [PageHeader.name]: PageHeader,
    [Button.name]: Button,
    [Drawer.name]: Drawer,
    [Input.name]: Input,
    [Icon.name]: Icon,
    [Tooltip.name]: Tooltip,
    [Alert.name]: Alert
  },
  data() {
    return {
      title: 'Luck Simple',
      subTitle: 'This is a game.',
      showEmpty: false,
      isShowAlert: false,
      isRunning: false,
      visible: false,
      luckMember: [],
      randomMember: {}
    }
  },
  methods: {
    go() {
      let lenght = this.dataSource.length
      if (lenght == 0) {
        this.$message.error('There\'s no option!')
        this.visible = true
        this.randomMember = {}
        return
      } else if (lenght == 1) {
        this.$message.warning('There\'s only one option!')
        this.visible = true
        return
      } else if (lenght == 2) {
        this.isShowAlert = true
      }
      this.isRunning = true
      let count = 0
      let interval = setInterval(() => {
        count++
        let random = this.random()
        let member = this.dataSource[random]
        this.randomMember = member
        if (count >= lenght * 4) {
          clearInterval(interval)
          this.isRunning = false
          this.dataSource.splice(random, 1)
          this.luckMember.push(member)
          this.openNotification(member)
          return
        }
      }, 77)
    },
    random() {
      let min = 0
      let max = this.dataSource.length - 1
      return min + Math.round(Math.random() * max)
    },
    openNotification(member) {
      this.$notification.config({
        placement: 'topLeft',
        top: '120px'
      })
      this.$notification.open({
        duration: null,
        message: member.title,
        description: member.subtitle,
        icon: <a-icon type="smile" style="color: #108ee9"/>,
      });
    },
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    handleRandomItem() {
      this.randomItem()
      this.luckMember = []
      this.randomMember = {}
    },
    handleClearCache() {

    },
    ...mapMutations(['randomItem']),
  },
  computed: {
    dataSource() {
      return this.$store.getters['luckCardModule/getDataSource']
    }
  },
  beforeDestroy() {
    this.$notification.destroy()
  }
}
</script>

<style lang="scss" scoped>

.content {
  top: 20px;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px 0;

  p {
    color: #333;
    font-size: 22px;
  }
}

.action {
  .van-button + .van-button {
    margin-top: 10px;
  }
}

</style>
